import apiCore from '@/utils/apiCore'

const HTTP = new apiCore({
  baseURL: process.env.VUE_APP_URL,
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
  },
})

export function saveProjectRequest(params: any) {
  return HTTP.post('/buss/project/save', params)
}
