import apiCore from '@/utils/apiCore'

const HTTP = new apiCore({
  baseURL: process.env.VUE_APP_URL,
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
  },
})

export function getProjectList() {
  return HTTP.get('/buss/project/select')
}
export function getSearchBar(projectId: string) {
  return HTTP.get('/buss/project/searchBar', { projectId: projectId })
}

/**
 * @Author roct
 * @Description 获取项目房间查询总览
 * @Date 11:47 2021/5/30 0030
 **/
export function getSearchAll(param: any) {
  return HTTP.get('/buss/room/searchAll', param)
}
